@import "../../global.scss";
.home{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 3;
    background-color: #000;
    
    @include mobile_potrait{
    }
    .homeContent{
        width: 100%;
        height: calc(100vh - 7rem);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: relative;
        @include mobile_landscape{
            height: calc(100vh - 5rem);
        }
        @include mobile_potrait{
            height: calc(100vh - 5rem);
            
        }
        .homeImg{
            width: 100%;
            position: absolute;
            top:0;
            left: 0;
            @include mobile_landscape{
                height: 100%;
            }
            @include mobile_potrait{
                height: 100%;
                object-fit: cover;
                
            }
        }
        .videoEmbed{
            height: 100%;
            width: 100%;
            position: absolute;
            object-fit: cover;

        }
        p{
            color: white;
            font-size: 1.5rem;
            margin-bottom: 1rem;
            z-index: 5;
            @include mobile_potrait{
                width: 80%;
                text-align: center;
                font-size: 1rem;
            }
            @include mobile_landscape{
                width: 80%;
                text-align: center;
                font-size: 1rem;
            }
            
        }
        h1{
            color: white;
            font-size: 5rem;
            margin-bottom: 1rem;
            z-index: 5;
            @include mobile_potrait{
                width: 90%;
                text-align: center;
                font-size: 2rem;
            }
            @include mobile_landscape{
                width: 90%;
                text-align: center;
                font-size: 2rem;
            }
        }
        .countDown{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            z-index: 1;
        }
        .gotoMint{

            width: 10rem;
            height: 3.5rem;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px white solid;
            border-radius: 2rem;
            background-color: rgba(255, 255, 255, 0);
            color: white;
            font-size: 1.2rem;
            cursor: pointer;
            transition: all 0.3s ease;
            z-index: 1;
            text-decoration: none;
            &:hover{
                background-color: rgba(255, 255, 255, 0.068);
            }
            @include mobile_potrait{
                width: 8rem;
                height: 2.5rem;
                font-size: 1rem;
            }
            @include mobile_landscape{
                width: 8rem;
                height: 2.5rem;
                font-size: 1rem;
            }
            
        }
        .muteButton{
            width: 3rem;
            height: 3rem;
            font-size: 2rem;
            color: white;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            bottom:3rem;
            right:2rem;
            cursor: pointer;
            @include mobile_landscape{
                bottom:1rem;
                right:1rem;
                font-size: 1.5rem;
            }
            @include mobile_potrait{
                bottom:1rem;
                right:1rem;
                font-size: 1.5rem;
                
            }
        }
    }
}
