@import "../../global.scss";
.mintPage{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 3;
    background-color: #000;
    position: relative;
    overflow: hidden;
    @include mobile_potrait{
    }
    .bg{
        position: absolute;
        width: 100%;
    }
    .mintPageContent{
        width: 90%;
        height: calc(100vh - 12rem);
        display: flex;
        margin-top: 5rem;
        
        @include mobile_landscape{
            margin-top: 2rem;
            height: calc(100vh - 5rem);
        }
        @include mobile_potrait{
            flex-direction: column;
            margin-top: 2rem;
            height: calc(100vh - 5rem);
        }
        
        .left{
            flex: 0.6;
            height: 80%;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow-x: hidden;
            position: relative;
            
            @include mobile_potrait{
                flex: 0.5;
            }
            .wrapper{
                width: 100%;
                height: 90%;
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                flex-direction: column;
                // background-image: linear-gradient(45deg, #daffda 0%, #e9fbff 100%);
                border-radius: 2rem;
                @include mobile_potrait{
                    width: 90%;
                    height: 90%;
                    border-radius: 2rem;
                    align-items: center;
                }
                @include mobile_landscape{
                    border-radius: 1rem;
                }
                .header{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    flex-direction: row;
                    .price{
                        width: 50%;
                        display: flex;
                        align-items: flex-start;
                        justify-content: center;
                        flex-direction: column;
                        
                        h1{
                            font-size: 4rem;
                            color: white;
                            font-style: italic;
                            line-height: 3rem;
                            font-family: 'Work Sans', sans-serif;
                            @include mobile_potrait{
                                font-size: 2rem;
                                line-height: 1.5rem;
                            }
                            @include mobile_landscape{
                                font-size: 2rem;
                                line-height: 1.5rem;
                            }
                            span{
                                font-size: 1.5rem;
                                font-weight: normal;
                                font-family: 'Work Sans', sans-serif;
                                @include mobile_potrait{
                                    font-size: 0.6rem;
                                }
                                @include mobile_landscape{
                                    font-size: 0.6rem;
                                }
                            }
                        }
                        p{
                            font-size: 1rem;
                            color: white;
                            font-family: 'Work Sans', sans-serif;
                            @include mobile_potrait{
                                font-size: 0.6rem;
                            }
                            @include mobile_landscape{
                                font-size: 0.6rem;
                            }
                        }
                    }
                    .count{
                        width: 50%;
                        display: flex;
                        align-items: flex-start;
                        justify-content: center;
                        flex-direction: column;
                        h1{
                            font-size: 4rem;
                            color: white;
                            font-style: italic;
                            line-height: 3rem;
                            font-family: 'Work Sans', sans-serif;
                            @include mobile_potrait{
                                font-size: 1.5rem;
                                line-height: 1.5rem;
                            }
                            @include mobile_landscape{
                                font-size: 2rem;
                                line-height: 1.5rem;
                            }
                            span{
                                font-size: 1.5rem;
                                font-weight: normal;
                                font-family: 'Work Sans', sans-serif;
                                @include mobile_potrait{
                                    font-size: 0.6rem;
                                }
                                @include mobile_landscape{
                                    font-size: 0.6rem;
                                }
                            }
                        }
                    }
                }
                .mintCount{
                    width: 80%;
                    height: 10rem;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @include mobile_potrait{
                        height: 5rem;
                    }
                    @include mobile_landscape{
                        height: 5rem;
                    }
                    .mintDecrease{
                        width: 4rem;
                        height: 4rem;
                        transition: all 0.5s ease;
                        cursor: pointer;
                        background: linear-gradient(180deg, #8912B8 0%, #2B003C 100%);
                        border: none;
                        border-radius: 10px;
                        color:rgb(255, 255, 255);
                        // text-shadow: 1px 1px 3px black;
                        font-size: 2.5rem;
                        font-weight: 600;
                        margin-left: 2rem;
                        &:hover{
                            transform: scale(1.1);
                        }
                        @include mobile_potrait{
                            width: 2rem;
                            height: 2rem;
                            font-size: 1rem;
                            margin-left: 0.5rem;
                            border-radius: 0.3rem;
                        }
                        @include mobile_landscape{
                            width: 2rem;
                            height: 2rem;
                            font-size: 1rem;
                            margin-left: 0.5rem;
                            border-radius: 0.3rem;
                        }
                    }
                    .mintIncrease{
                        width: 4rem;
                        height: 4rem;
                        transition: all 0.5s ease;
                        cursor: pointer;
                        background: linear-gradient(180deg, #8912B8 0%, #2B003C 100%);
                        border: none;
                        border-radius: 10px;
                        color:rgb(255, 255, 255);
                        // text-shadow: 1px 1px 3px black;
                        font-size: 2.5rem;
                        font-weight: 600;
                        font-family: 'Work Sans', sans-serif;
                        &:hover{
                            transform: scale(1.1);
                        }
                        @include mobile_potrait{
                            width: 2rem;
                            height: 2rem;
                            font-size: 1rem;
                            border-radius: 0.3rem;
                        }
                        @include mobile_landscape{
                            width: 2rem;
                            height: 2rem;
                            font-size: 1rem;
                            border-radius: 0.3rem;
                        }
                    }
                    .mintCountValue{
                        font-size: 5rem;
                        font-weight: 700;
                        margin: 1rem;
                        color: #ffffff;
                        font-family: 'Work Sans', sans-serif;
                        @include mobile_potrait{
                            font-size: 2rem;
                            margin: 0;
                        }
                        @include mobile_landscape{
                            font-size: 2rem;
                            
                        }
                    }
                    .mintButton{
                        padding: 0.3rem 3rem;
                        cursor: pointer;
                        border: none;
                        transition: all 0.5s ease;
                        position: relative;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background: linear-gradient( 180deg, #B67B00 0%, #842A21 100%);
                        border-radius: 0.5rem;
                        color: white;
                        text-shadow: 1px 1px 3px black;
                        margin-right: 2rem;
                        @include mobile_potrait{
                            font-size: 1rem;
                            padding: 0.3rem 0.8rem;
                            margin-right: 0.5rem;
                            border-radius: 0.3rem;
                        }
                        @include mobile_landscape{
                            font-size: 1rem;
                            padding: 0.3rem 0.8rem;
                            margin-right: 0.5rem;
                            border-radius: 0.3rem;
                        
                        }
                        &:hover{
                            transform: scale(1.1);
                        }
                        span{
                            width: 100%;
                            font-size: 3rem;
                            font-weight: 700;
                            z-index: 1;
                            text-align: center;
                            @include mobile_potrait{
                                font-size: 1rem;
                            }
                            @include mobile_landscape{
                                font-size: 1rem;
                            
                            }
                        }
                    }
                }
                .bottom{
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    flex-direction: row;
                    color:#6b6b6b;
                    @include mobile_potrait{
                        border-radius: 0.5rem;
                        margin-bottom: 1rem;
                    }
                    @include mobile_landscape{
                        height: 2.5rem;
                        border-radius: 0.5rem;
                        margin-bottom: 1rem;
                    }
                    span{
                        text-align: center;
                        font-size: 2.5rem;
                        font-family: 'Work Sans', sans-serif;
                        font-style: italic;
                        font-weight: 600;
                        color: #fff;
                        @include mobile_potrait{
                            font-size: 0.8rem;
                            margin-bottom: 0rem;
                        }
                        @include mobile_landscape{
                            font-size: 0.8rem;
                            margin-bottom: 0rem;
                        }
                    }
                    div{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-direction: column;
                        img{
                            margin-bottom: 1rem;
                            @include mobile_potrait{
                                width: 2rem;
                                margin-bottom: 0.5rem;
                            }
                            @include mobile_landscape{
                                width: 2rem;
                                margin-bottom: 0.5rem;
                            }
                        }
                        .plus{
                            width: 1rem;
                        }
                        p{
                            font-size: 1rem;
                            color: white;
                            @include mobile_potrait{
                                font-size: 0.8rem;
                            }
                            @include mobile_landscape{
                                font-size: 0.8rem;
                                
                            }
                        }
                    }
                }
                
            }
        }
        .right{
            flex: 0.4;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            position: relative;
            height: 80%;
            @include mobile_potrait{
                border-radius: 2rem;
            }
            @include mobile_landscape{
                border-radius: 2rem;
            }
            img{
                height: 100%;
                @include mobile_potrait{
                    width: 10rem;
                }
                @include mobile_landscape{
                    width: 10rem;
                }
            }
            p{
                font-size: 1.5rem;
                color: white;
                text-align: center;
                width: 70%;
                @include mobile_potrait{
                    font-size: 0.8rem;
                }
                @include mobile_landscape{
                    font-size: 0.8rem;
                }
            }
        }
    }
}