@import "../../global.scss";
.footer{
    width: 100%;
    // height: 40vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #5024AB;
    @include mobile_potrait{
        // height: 30vh;
    }
    @include mobile_landscape{
        // height: 50vh;
    }
    h1{
        font-size: 2rem;
        color: #ffffff;
        margin-bottom: 1rem;
        margin-top: 2rem;
        @include mobile_potrait{
            font-size: 1rem;
            margin-bottom: 0.5rem;
        }
        @include mobile_landscape{
            font-size: 1rem;
            margin-bottom: 0.5rem;
        }
    }
    p{
        font-size: 1rem;
        color: #ffffff;
        width: 70%;
        text-align: center;
        margin-bottom: 1rem;
        @include mobile_potrait{
            font-size: 0.8rem;
            margin-bottom: 0.5rem;
            width: 90%;
        }
        @include mobile_landscape{
            font-size: 0.8rem;
            margin-bottom: 0.5rem;
            width: 90%;
        }
    }
    .myForm{
        display: flex;
        align-items: center;
        justify-content:center;
        margin-bottom: 1rem;
        input{
            padding: 0.8rem;
            height: 1.4rem;
            width: 18rem;
            border: none;
            background-color: rgba(255, 255, 255, 0.253);
            border-radius: 0.5rem;
            &:focus{
                outline: none;
                // background-color: rgba(230, 223, 223, 0);
                
            }
            &::placeholder {
                color: rgb(255, 255, 255);
            }
            @include mobile_potrait{
                height: 0.8rem;
                width: 12rem;
                font-size: 0.8rem;
            }
            @include mobile_landscape{
                height: 0.8rem;
                width: 12rem;
                font-size: 0.8rem;
            }
        }
        button{
            height: 3rem;
            background-color: #FF4800;
            border: none;
            width: 7rem;
            color: white;
            margin-left: 0.5rem;
            border-radius: 0.5rem;
            cursor: pointer;
            @include mobile_potrait{
                height: 2.2rem;
                width: 7em;
                font-size: 0.8rem;
            }
            @include mobile_landscape{
                height: 2.2rem;
                width: 7em;
                font-size: 0.8rem;
            }
        }
    }
    .footerContent{
        width: 90%;
        height: 90%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        .socialLinks{
            width: 100%;
            display: flex;
            align-items:center;
            justify-content: center;
            margin-bottom: 3rem;
            @include mobile_potrait{
                margin-bottom: 1rem;
            }
            @include mobile_landscape{
                margin-bottom: 1rem;
            }
            a{
                display: flex;
                color: white;
                margin-left: 1rem;
                width: 2.5rem;
                height: 2.5rem;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: #fff;
                color: #5024AB;
                text-decoration: none;
                border-radius: 50%;
                font-size: 1.2rem;
                @include mobile_potrait{
                    width: 1.6rem;
                    height: 1.6rem;
                    font-size: 0.8rem;
                }
                @include mobile_landscape{
                    width: 1.6rem;
                    height: 1.6rem;
                    font-size: 0.8rem;
                }
            }
        }
    }
}