@import "../../global.scss";
.stroy{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    margin-top: 2rem;
    h1{
        color:white;
        font-size: 3rem;
        margin-bottom: 1rem;
        @include mobile_potrait{
            font-size: 1.5rem;
        }
        @include mobile_landscape{
            font-size: 1.5rem;
        }
    }
    p{
        color: #fff;
        width: 50%;
        text-align: center;
        font-size: 1rem;
        margin-bottom: 3rem;
        @include mobile_potrait{
            font-size: 0.8rem;
            width: 80%;
        }
        @include mobile_landscape{
            font-size: 0.8rem;
            width: 80%;
        }

    }
    a{
        padding: 0.8rem 1.5rem;
        background-color: #fff;
        text-decoration: none;
        border-radius: 2rem;
        margin-bottom: 1rem;
        @include mobile_potrait{
            font-size: 0.8rem;
            padding: 0.5rem 1rem;
        }
        @include mobile_landscape{
            font-size: 0.8rem;
            padding: 0.5rem 1rem;
        }
    }
    .stroyContent{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;
        z-index: 1;
        @include mobile_potrait{
            // display: none;
        }
        .nodeLine{
            width: fit-content;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
        }
        .node{
            display: flex;
            width: 80%;
            // margin-bottom:1rem;
            .left{
                flex: 0.5;
            }
            .right{
                flex: 0.5;
            }
            div{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                img{
                    width: 70%;
                    @include mobile_potrait{
                        width: 100%;
                    }
                    @include mobile_landscape{
                        width: 70%;
                    }
                }
                h2{
                    font-size: 1.5rem;
                    color: white;
                    margin-bottom: 2rem;
                    font-size: 2rem;
                    text-align: center;
                    @include mobile_potrait{
                        font-size: 0.8rem;
                        margin-bottom: 0.5rem;
                    }
                    @include mobile_landscape{
                        font-size: 0.8rem;
                        margin-bottom: 0.5rem;
                    }
                }
                p{
                    font-size: 1.5rem;
                    width: 80%;
                    
                    @include mobile_potrait{
                        font-size: 0.5rem;
                        margin-bottom: 1rem;
                    }
                    @include mobile_landscape{
                        margin-bottom: 1rem;
                        font-size: 0.5rem;
                    }
                }
                .btns{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: row;
                    @include mobile_potrait{
                        flex-direction: column;
                    }
                    .discord{
                        width: 12rem;
                        height: 1.8rem;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-image: linear-gradient(to right,#B508E0, rgb(133, 14, 176));
                        border-radius: 2rem;
                        margin-right: 1rem;
                        cursor: pointer;
                        margin-bottom: 0;
                        text-decoration: none;
                        color: white;
                        @include mobile_potrait{
                            width: 8rem;
                            height: 1.8rem;
                            font-size: 0.6rem;
                            margin-bottom: 0.5rem;
                            margin-right: 0rem;
                            padding: 0;
                        }
                        @include mobile_landscape{
                            width: 8rem;
                            height: 1.8rem;
                            font-size: 0.6rem;
                            padding: 0;
                        }
                    }
                }
            }
        }
        .road{
            margin-bottom: 1rem;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            img{
                width: 40%;
            }
            
        }
    }
    .storyBg{
        width: 100%;
        position: absolute;
        top:0;
        left: 0;
        img{
            width: 100%;
            @include mobile_potrait{
                object-fit: cover;
            }
        }
    }
}