@import "../../global.scss";
.planets{
    width: 100%;
    height: 120vh;
    display: flex;
    align-items: center;
    justify-content: center;
    @include mobile_landscape{
        height: 170vh;
    }
    .planetsContent{
        width: 90vw;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        // background-color: #5024AB;
        border-radius: 2rem;
        overflow: hidden;
        .top{
            width: 100%;
            height: 50%;
            display: flex;
            @include mobile_potrait{
                flex-direction: column;
                height: 70%;
            }
            .topLeft{
                flex: 0.6;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow-x: hidden;
                position: relative;
                @include mobile_potrait{
                    flex: 0.6;
                }
                .sliderViwe{
                    display: flex;
                    align-items: flex-start;
                    justify-content: center;
                    width: 216vw;
                    height: 90%;
                    z-index: 2;
                    left: 0;
                    overflow: hidden;
                    position: absolute;
                    animation: slide 15s linear infinite;
                    // animation: name duration timing-function delay iteration-count direction fill-mode;
                    // animation-play-state: paused;
                    // transform: translateX(calc(-25% * 2));
                    @keyframes slide {
                        0%{
                            transform: translateX(0);
                        }
                        5%{
                            transform: translateX(calc(-54vw ));

                        }
                        33%{
                            transform: translateX(calc(-54vw ));

                        }
                        38%{
                            transform: translateX(calc(-54vw * 2));

                        }
                        66%{
                            transform: translateX(calc(-54vw * 2));
                        }
                        71%{
                            transform: translateX(calc(-54vw * 3));
                        }
                        100%{
                            transform: translateX(calc(-54vw * 3));
                        }
                        
                    }
                    @include mobile_potrait{
                        width: 360vw;
                        @keyframes slide {
                            0%{
                                transform: translateX(0);
                            }
                            5%{
                                transform: translateX(calc(-90vw ));
    
                            }
                            33%{
                                transform: translateX(calc(-90vw ));
    
                            }
                            38%{
                                transform: translateX(calc(-90vw * 2));
    
                            }
                            66%{
                                transform: translateX(calc(-90vw * 2));
                            }
                            71%{
                                transform: translateX(calc(-90vw * 3));
                            }
                            100%{
                                transform: translateX(calc(-90vw * 3));
                            }
                            
                        }
                    }
                    .railImg{
                        width: 53vw;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin: 1vw;
                        overflow-x: hidden;
                        @include mobile_potrait{
                            width: 88vw;
                        }
                        img{
                            height: 100%;
                            z-index: 2;
                            @include mobile_potrait{
                                width: 100%;
                                height: auto;
                                object-fit: cover;
                            }
                        }
                        
                    }
                }
            }
            .topRight{
                flex: 0.4;
                background-color: #000000;
                display: flex;
                align-items: flex-end;
                justify-content: center;
                overflow: hidden;
                position: relative;
                img{
                    height: 90%;
                    @include mobile_potrait{
                    }
                }
                .learnMore{
                    position: absolute;
                    color:white;
                    font-size: 1.5rem;
                    margin-bottom: 2rem;
                    text-decoration: none;
                    @include mobile_potrait{
                        font-size: 0.8rem;
                        margin-bottom: 1rem;
                    }
                    @include mobile_landscape{
                        font-size: 0.8rem;
                    }
                }

            }
        }
        .bottom{
            width: 100%;
            height: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            flex-direction: column;
            @include mobile_potrait{
                height: 30%;
            }
            h1{
                color:white;
                font-size: 3rem;
                margin-bottom: 2rem;
                margin-top: 5rem;
                @include mobile_potrait{
                    font-size: 1.5rem;
                    margin-bottom: 0rem;
                    margin-top: 0rem;
                }
                @include mobile_landscape{
                    font-size: 1.5rem;
                }
            }
            .bottomSlideView{
                width:90%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                overflow: hidden;
                @include mobile_potrait{
                    width: 90vw
                }
                @include mobile_landscape{
                    // width: 42rem;
                }
                .bottomSlideList{
                    width: 288vw;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transition: all 0.3s ease;
                    position: absolute;
                    left: 0;
                    @include mobile_potrait{
                        width: 540vw;
                    }
                    @include mobile_landscape{
                        // width: 42rem;
                    }
                    img{
                        width: 14vw;
                        transition: all 0.3s ease;
                        margin: 1vw;
                        // position: absolute;
                        cursor: pointer;
                        @include mobile_potrait{
                            width: 28vw;

                        }
                        @include mobile_landscape{
                        }
                    }
                
                }
                
            }
            .dotList{
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                bottom: 1rem;
                
                .dot{
                    width: 1rem;
                    height: 0.2rem;
                    background-color: #fff;
                    margin: 0.5rem;
                    border-radius: 1rem;
                    opacity: 0.5;
                    cursor: pointer;
                    transition: all 0.3s ease;
                    @include mobile_potrait{
                        width: 0.6rem;
                        height: 0.6rem;
                    }
                    @include mobile_landscape{
                        width: 0.6rem;
                        height: 0.6rem;
                    }
                }
            }
        }
    }
}