@import "../../global.scss";
.timer{
    width: 100%;
    .timerNums{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: rgb(201, 201, 201);
        font-size: 2rem;
        overflow: hidden;
        @include mobile_potrait{
            font-size: 1rem;
        }
        @include mobile_landscape{
            font-size: 1rem;
        }
        div{
            display: flex;
            align-items: center;
            justify-content: space-around;
            flex-direction: column;
            
            @include mobile_potrait{
            }
            @include mobile_landscape{
            }
            .number{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: row;
                overflow: hidden;
                color: #ffffff;
                
                font-weight: 500;
                font-size: 3rem;
                text-shadow: 2px 2px 0px black;
                margin: 0.5rem;
                @include mobile_potrait{
                    margin: 0.2rem;
                }
                @include mobile_landscape{
                    margin: 0.1rem;
                }
                span{
                    width: 3rem;
                    height: 4rem;
                    background-color: rgba(255, 255, 255, 0.6);
                    font-size: 3.25rem;
                    font-weight: 900;
                    margin: 0.2rem;
                    border-radius: 0.5rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: relative;
                    @include mobile_potrait{
                        font-size: 2rem;
                        width: 1.8rem;
                        height: 2.8rem;
                        border-radius: 0.2rem;
                        margin: 0.1rem;
                    }
                    @include mobile_landscape{
                        font-size: 2rem;
                        width: 2rem;
                        height: 2.5rem;
                        border-radius: 0.2rem;
                        margin: 0.1rem;
                    }
                    .bottom{
                        width: 100%;
                        height: 50%;
                        position: absolute;
                        background-color: rgba(0, 0, 0, 0.233);
                        bottom: 0;
                        border-bottom-left-radius: 0.5rem;
                        border-bottom-right-radius: 0.5rem;
                        @include mobile_potrait{
                            border-bottom-left-radius: 0.2rem;
                            border-bottom-right-radius: 0.2rem;
                        }
                        @include mobile_landscape{
                            border-bottom-left-radius: 0.2rem;
                            border-bottom-right-radius: 0.2rem;
                        }
                    }
                }
                
            }
            .text{
                font-size: 1.2rem;
                margin-bottom: 3px;
                overflow: hidden;
                color: rgb(255, 255, 255);
                text-shadow: 2px 2px 0px black;
                @include mobile_potrait{
                    font-size: 0.8rem;
                    margin-top: 0vh;
                }
                @include mobile_landscape{
                    font-size: 0.8rem;
                    margin-top: 0vh;
                }
            }
            
        }
        
        span{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

.circularTimer{
    width: 100%;
    .circularTimerNums{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        .number{
            width: 90%;
            height: 70%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            color: rgb(0, 0, 0);
            font-size: 1rem;
            margin: 1rem;
            
            @include mobile_potrait{
                margin: 0.5rem;
            }
            @include mobile_landscape{
                margin: 0.5rem;
            }
            .nums{
                color: white;
                font-size: 4rem;
                line-height: 4rem;
                font-family: 'Roboto', sans-serif;
                @include mobile_potrait{
                    font-size: 2rem;
                    line-height: 2rem;
                }
                @include mobile_landscape{
                    font-size: 2rem;
                    line-height: 2rem;
                }
            }
            .CircularProgressbar {
                width: 7rem !important;
                height: 7rem!important;
                display: flex;
                align-items: center;
                justify-content: center;
                @include mobile_potrait{
                    width: 3.5rem !important;
                    height: 3.5rem!important;
                }
                @include mobile_landscape{
                    width: 3.5rem !important;
                    height: 3.5rem!important;
                }

            }
            span{
                color: white;
                font-size: 1.5rem;
                font-family: 'Roboto', sans-serif;
                @include mobile_potrait{
                    font-size: 1rem;
                }
                @include mobile_landscape{
                    font-size: 1rem;
                }
            }
        }
    }
}