@import "../../global.scss";
.connectModal{
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.61);
    z-index: 99999;
    display: none;
    
    &.active{
        display: flex;
    }
    .modelContent{
        z-index: 5;
        width: 20rem;
        height: 23rem;
        background: rgb(51, 51, 51);
        margin: auto;
        border-radius: 2rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        border: 2px #0e1759 solid;
        z-index: 99999;
        @include mobile_potrait{
            transform: scale(0.8);
            border-radius: 1rem;
        }
        @include mobile_landscape{
            transform: scale(0.8);
            border-radius: 1rem;
        }
        
        .connectWalletHeader{
            width: 100%;
            height: 5rem;
            background: linear-gradient(139.73deg, rgb(7, 79, 87) 0%, rgb(40, 29, 73) 100%);
            border-bottom: 1px solid rgb(231, 227, 235);
            border-top-left-radius: 2rem;
            border-top-right-radius: 2rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include mobile_potrait{
                border-top-left-radius: 1rem;
                border-top-right-radius: 1rem;
            }
            @include mobile_landscape{
                border-top-left-radius: 1rem;
                border-top-right-radius: 1rem;
            }
            .connectWalletTitle{
                font-size: 1.5rem;
                padding-left: 20px;
                // font-family: 'Bangers', cursive;
                font-weight: normal;
                color: #ffffff;
                text-shadow: 1px 1px 3px black;
            }
            .connectModalCloseButton{
                color:black;
                font-size: 16px;
                font-weight: 600;
                border: 0px;
                border-radius: 16px;
                cursor: pointer;
                color: #ffffff;
                margin-right: 20px;
                padding: 0.5rem 1rem;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: rgb(42, 40, 114);
                box-shadow: 1px 1px 3px black;
            }
        }
        
        .connectWalletWrapper{
            width: 90%;
            height: 17em;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            flex-direction: column;
            
            div{
                width: 100%;
                display: flex;
                cursor: pointer;
                margin-top: 0.8rem;
                .left{
                    flex: 0.2;
                }
                .middle{
                    flex: 0.7;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    h2{
                        font-size: 1rem;
                        width: 90%;
                        color: white;
                    }
                    p{
                        font-size: 0.8rem;
                        width: 90%;
                        color: rgb(153, 153, 153);
                    }
                }

                .right{
                    flex: 0.1;
                    button{
                        border: none;
                        background-color: #ffffff00;
                    }
                }
               
            }
            .metaMask{
                .left{
                    .icon{
                        width: 4rem;
                        height: 4rem;
                        background-color: rgb(255, 219, 219);
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;
                        img{
                            width: 80%;
                            height: 80%;
                        }
                    }
                }
                
            }
            .wallet{
                .left{
                    .icon{
                        width: 4rem;
                        height: 4rem;
                        background-color: rgb(220, 219, 255);
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;
                        img{
                            width: 80%;
                            height: 80%;
                        }
                    }
                }
            }
        }
    }
}
