@import "../../global.scss";
.team{
    width: 100%;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    margin-top: 3rem;
    @include mobile_potrait{
        height: 80vh;
    }
    @include mobile_landscape{
        height: 80vh;
    }

        h1{
        color:white;
        font-size: 3rem;
        margin-bottom: 5rem;
        @include mobile_potrait{
            font-size: 1.5rem;
        }
        @include mobile_landscape{
            font-size: 1.5rem;
        }
    }
    .teamContent{
        width: 100%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        position: relative;
        z-index: 10;
        @include mobile_potrait{
        }
        
        .teamWrapper{
            width: 90%;
            display: flex;
            align-items: flex-start;
            justify-content: center;
            flex-wrap: wrap;
            @include mobile_potrait{
            }
            @include mobile_landscape{
                height: 100%;
            }
                .teamItem{
                    width: 13%;
                    // height: 7rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    margin: 0.5%;
                    // background-color: #ffffff50;
                    border-radius: 5rem;
                    margin-bottom: 2rem;
                    @include mobile_potrait{
                        width: 30%;
                    }
                    @include mobile_landscape{
                        width: 13%;
                    }
                    a{
                        width: 5rem;  
                        height: 5rem;
                        background-color: #ffffff50;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-bottom: 1rem;
                        @include mobile_potrait{
                            width: 3rem;  
                            height: 3rem;
                            margin-bottom: 0.5rem;
                        }
                        @include mobile_landscape{
                            width: 3rem;  
                            height: 3rem;
                            margin-bottom: 0.5rem;
                        }
                        img{
                            width: 4rem;
                            cursor: pointer;
                            border-radius: 50%;
                            @include mobile_potrait{
                                width: 2rem;
                            }
                            @include mobile_landscape{
                                width: 2rem;
                            }
                        }
                    }
                   
                        .name{
                            width: 100%;
                            color: #fff;
                            font-size: 1.5rem;
                            font-weight: normal;
                            text-align: center;
                            margin-bottom: 1rem;
                            @include mobile_potrait{
                                font-size: 0.8rem;
                                margin-bottom: 0.5rem;
                            }
                            @include mobile_landscape{
                                font-size: 0.8rem;
                                margin-bottom: 0.5rem;
                            }
                        }
                        p{
                            width: 100%;
                            color: #fff;
                            font-size: 1rem;
                            font-weight: normal;
                            text-align: center;
                            @include mobile_potrait{
                                font-size: 0.6rem;
                            }
                            @include mobile_landscape{
                                font-size: 0.6rem;
                            }
                        }
                    
            }
            
        }
    }
    .teamBg{
        position: absolute;
        left: 0;
        bottom: 0;
        height: 70vh;
        display: flex;
        align-items: flex-end;
        justify-content: flex-start;
        @include mobile_landscape{
            height: 100%;
        }
        img{
            @include mobile_potrait{
                width: 50%;
            }
            @include mobile_landscape{
                width: 50%;
                height: 100%;
            }
        }
        
    }
}