$width : 450px;
$bg_color: #101010;
$str_color:rgb(60, 190, 112);
@mixin mobile_potrait{
    @media screen and (max-width: $width) and (orientation:portrait){
        @content
    }
};
@mixin mobile_landscape{
    @media screen and (max-height: $width) and (orientation:landscape){
        @content
    }
};

@mixin button1(){
    border: none;
    background-color: rgba(241, 196, 15, 1);
    transition: all 0.3s ease;
    color:rgb(0, 0, 0);
    font-size: 2vh;
    border-radius: 2.5vh;
    cursor: pointer;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    @include mobile_potrait{
        font-size: 1.5vh;
    }
    &:hover{
        background-color: rgb(255, 220, 81);
    }
    img{
        height: 40%;
        margin-right: 10px;
    }
}
