@import "../../global.scss";
.roadmap{
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
    height: 400vh;
    margin-top: 3rem;
    z-index: 1;
    scroll-behavior: smooth;
    .roadmapContent{
        position: sticky;
        width: 90%;
        height: 100vh;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-direction:row;
        top: 0;
        z-index: 1;
        .left{
            width: 80%;
            height: 100vh;
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            position: relative;
            .roadmapTitle{
                width: 100%;
                margin-top: 10vh;
                margin-bottom: 0rem;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                
                @include mobile_potrait{
                    width: 90%;
                    margin-bottom: 1rem;
                }
                @include mobile_landscape{
                    width: 90%;
                    margin-bottom: 1rem;
                }
                h1{
                    font-family: 'Oswald', sans-serif;
                    font-size: 3rem;
                    color: white;
                    width: 90%;
                    // text-align: center;
                    @include mobile_potrait{
                        font-size: 1.8rem;
                    }
                    @include mobile_landscape{
                        font-size: 1.8rem;
                    }
                }
            }
            @include mobile_potrait{
                flex-direction: column;
                overflow: hidden;
            }
            @include mobile_landscape{
                width: 90%;
            }
            .node{
                transition: all 0.5s ease;
                width: 100%;
                height: 80vh;
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-direction: column;
                position: absolute;
                top: 17vh;
                z-index: 1;
                @include mobile_potrait{
                    margin-bottom: 1rem;
                    width: 90%;
                }
                @include mobile_landscape{
                    width: 90%;
                }
                
                .title{
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    flex-direction: column;
                    width: 100%;
                    margin-bottom: 2rem;
                    @include mobile_potrait{
                        flex-direction: column;
                        margin-bottom: 1rem;
                    }
                    @include mobile_landscape{
                        margin-bottom: 0.5rem;
                    }
                    h2{
                        color: rgb(187, 187, 187);
                        font-size: 2rem;
                        font-weight: 500;
                        width: 90%;
                        @include mobile_potrait{
                            font-size: 1rem;
                        }
                        @include mobile_landscape{
                            font-size: 1rem;
                        }
                    }
                }
                div{
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    @include mobile_potrait{
                        width: 100%;
                    }
                    p{
                        width: 90%;
                        font-size: 1.5rem;
                        display: flex;
                        align-items: center;
                        color: gray;
                        margin-bottom: 0.5rem;
                        margin-left: 2rem;
                        @include mobile_potrait{
                            font-size: 0.8rem;
                            margin-bottom: 0.2rem;
                            margin-left: 0.5rem;
                        }
                        @include mobile_landscape{
                            font-size: 0.7rem;
                            margin-bottom: 0.2rem;
                        }
                    }

                    h2{
                        color: rgb(181, 181, 181);
                        font-size: 2rem;
                        font-weight: 500;
                        width: 90%;
                        margin-bottom: 0.5rem;
                        @include mobile_potrait{
                            font-size: 1rem;
                        }
                        @include mobile_landscape{
                            font-size: 0.8rem;
                            margin-bottom: 0.2rem;
                        }
                    }
                }
            }
            
        }
        .right{
            width: 20%;
            height: 100vh;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            position: relative;
            @include mobile_potrait{
                width: 30%;
            }
            .phase{
                height: 80%;
                width: 60%;
                display: flex;
                align-items: center;
                justify-content: space-around;
                flex-direction: column;
                margin-left: 10%;
                transition: all 0.3s ease;
                a{
                    color: white;
                    font-size: 1.5rem;
                    cursor: pointer;
                    text-decoration: none;
                    @include mobile_potrait{
                        font-size: 0.6rem;
                    }
                    @include mobile_landscape{
                        font-size: 0.6rem;
                    }
                }
            }
            .line{
                width: 3px;
                height: 90%;
                background-color: #fff;
                position: absolute;
                top:5%;
                right: 20%;

            }
            .earth{
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                width: 20%;
                z-index: 1;
                right: 10%;
                background-color: #000;
                border-radius: 50%;
                img{
                    width: 100%;
                }

            }
            
        }
    }
    .roadMapBg{
        width: 100%;
        position: absolute;
        top:0;
        left: 0;
        img{
            width: 100%;
            @include mobile_potrait{
                object-fit: cover;
            }
        }
    }
    .timeLine{
        height: 300vh;
        display: flex;
        align-items: center;
        justify-content:space-around;
        flex-direction: column;
        position: absolute;
        background-color: rgb(48, 48, 48);
        opacity: 0;
        div{
            color: white;
        }
    }
    
}