@import "../../global.scss";
.topbar{
    width: 100%;
    height: 7rem;
    background-color: rgba(0, 0, 0, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    flex-direction: column;
    position: relative;
    @include mobile_potrait{
        height: 5rem;
    }
    @include mobile_landscape{
        height: 5rem;
    }
    .topContent{
        display: flex;
        align-items: center;
        justify-content:space-between;
        width: 90%;
        @include mobile_potrait{
        }
        .logo{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            @include mobile_potrait{
                
            }
            @include mobile_landscape{
                height: 4rem;
            }
            a{
                font-size: 2rem;
                font-weight: 500;
                color:#fff;
                text-decoration: none;
                display: flex;
                align-items: center;
                justify-content: center;
                font-style: italic;
                @include mobile_potrait{
                    font-size: 1rem;
                }
                @include mobile_landscape{
                    font-size: 1rem;
                }
                .logoImg{
                    height: 3rem;
                    @include mobile_potrait{
                        height: 2rem;
                    }
                    @include mobile_landscape{
                        height: 2rem;
                    }
                }
                span{
                    @include mobile_potrait{
                        display: none;
                    }
                }
    
            }
            
        }
        .links{
            display: flex;
            align-items: center;
            justify-content: center;
            a{
                display: flex;
                color: white;
                margin-left: 1rem;
                text-decoration: none;
                @include mobile_potrait{
                    margin-left: 0.5rem;
                }
                @include mobile_landscape{
                    margin-left: 0.5rem;
                }
                div{
                    background-color: #fff;
                    color: #000;
                    width: 3rem;
                    height: 3rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 1.2rem;
                    border-radius: 2rem;
                    transition: all 0.3s ease;
                    position: relative;
                    &:hover{
                        opacity: 0.8;
                    }
                    @include mobile_potrait{
                        width: 1.8rem;
                        height: 1.8rem;
                        font-size: 0.8rem;
                    }
                    @include mobile_landscape{
                        font-size: 0.8rem;
                        width: 2rem;
                        height: 2rem;
                    }
                }
                .discordButton{
                    p{
                        position: absolute;
                        bottom: -2rem;
                        color: #fff;
                        opacity: 0;
                        transition: all 0.3s ease;
                    }
                    &:hover{
                        p{
                            opacity: 1;
                        }
                    }
                }
                
            }
            .walletButton{
                padding: 0.8rem 2rem;
                font-size: 1.2rem;
                background-image: linear-gradient(to right, #B508E0, rgb(133, 14, 176));
                color: #fff;
                font-size: 1.2rem;
                border-radius: 2rem;
                margin-left: 2rem;
                cursor: pointer;
                transition: all 0.3s ease;
                @include mobile_potrait{
                    font-size: 0.5rem;
                    padding: 0.5rem 0.5rem;
                    margin-left: 0.5rem;
                }
                @include mobile_landscape{
                    font-size: 0.5rem;
                    padding: 0.5rem 0.5rem;
                }
                &:hover{
                    opacity: 0.8;
                }
            }
        }
    }
